/**
 * 请求接口封装
 */
import { http } from '@/utils/request'

// 触手检测接口
export const chushou = (mobile) => {
  return http({
    url: '/register/submit.htm',
    method: 'POST',
    params: {
      username: mobile,
      nickname: '熟练地讲述了',
      password: mobile,
      gender: 'male',
      verifyCode: '222222',
      areaCode: '86',
      device_stoken: ''
    }
  })
}
